import React, {Component} from 'react'
import classes from './Quiz.module.css'
import ActiveQuiz from '../../components/ActiveQuiz/ActiveQuiz'
import FinishedQuiz from '../../components/FinishedQuiz/FinishedQuiz'

class Quiz extends Component {
    state = {
      results: {},
      isFinished: false,
      activeQuestion: 0,
      answerState: null,
      quiz: [
        {
          question: 'Найпопулярніший бренд алкоголю в світі?',
          rightAnswerId: 2,
          id: 1,
          answers: [
            {text: 'Johnnie Walker', id: 1},
            {text: 'Smirnoff', id: 2},
            {text: 'Bacardi', id: 3},
            {text: 'Absolut', id: 4}
            ]
        },
        {
          question: 'Рівень споживання пива в Україні на душу населення за рік?',
          rightAnswerId: 3,
          id: 2,
          answers: [
            {text: '115 літрів', id: 1},
            {text: '67 літр', id: 2},
            {text: '41 літр', id: 3},
            {text: '29 літр', id: 4}
          ]
        },
        {
          question: 'Смертельна доза алкоголю в проміле?',
          rightAnswerId: 1,
          id: 3,
          answers: [
            {text: '5-6 ', id: 1},
            {text: '4,5', id: 2},
            {text: '11', id: 3},
            {text: '7', id: 4}
          ]
        },
        {
          question: 'Батьківщина рому?',
          rightAnswerId: 4,
          id: 4,
          answers: [
            {text: 'Ямайка', id: 1},
            {text: 'Іспанія', id: 2},
            {text: 'Коломия', id: 3},
            {text: 'Барбадос', id: 4}
          ]
        },
        {
          question: 'Кіко треба випити перед караоке в пабі-34?',
          rightAnswerId: 3,
          id: 5,
          answers: [
            {text: 'Літр горівеги', id: 1},
            {text: '5 літрів Хмільного міцного', id: 2},
            {text: 'Я стільки не вип\'ю', id: 3},
            {text: 'Все, що є в Репабліку', id: 4}
          ]
        },
        {
          question: 'Хто переможе на Алко-квізі?',
          rightAnswerId: 4,
          id: 6,
          answers: [
            {text: 'Зелений змій', id: 1},
            {text: 'Лисий чувак на барі', id: 2},
            {text: 'Печінка', id: 3},
            {text: '7 хокаге', id: 4}
          ]
        }
        ]
    }

    onAnswerClickHandler = answerId => {
      if (this.state.answerState) {
        const key = Object.keys(this.state.answerState)[0]
        if (this.state.answerState[key] === 'success') {
          return
        }
      }

      const question = this.state.quiz[this.state.activeQuestion]
      const results = this.state.results

      if (question.rightAnswerId === answerId) {
        if (!results[question.id]) {
          results[question.id] = 'success'
        }
        this.setState({
          answerState: { [answerId]: 'success' },
          results
        })

        const timeout = window.setTimeout(() => {
          if (this.isQuizFinished()) {
             console.log('FINISh')
            this.setState({
              isFinished: true
            })
          } else {
            this.setState({
              activeQuestion: this.state.activeQuestion + 1,
              answerState: null
            })
          }

          window.clearTimeout(timeout)
        }, 1000)

      } else {
        results[question.id] = 'error'
        this.setState({
          answerState: { [answerId]: 'error' },
          results
        })
      }
    }

  isQuizFinished() {
      return this.state.activeQuestion + 1 === this.state.quiz.length
  }

  retryHandler = () => {
      this.setState({
        results: {},
        isFinished: false,
        activeQuestion: 0,
        answerState: null
      })
  }

    render() {
        return(
            <div className={classes.Quiz}>
                <div className={classes.QuizWrapper}>
                    <h1>Alco-quiz</h1>
                  {
                    this.state.isFinished
                      ?  <FinishedQuiz
                            results={this.state.results}
                            quiz={this.state.quiz}
                            onRetry={this.retryHandler}
                         />
                      :  <ActiveQuiz
                            answers={this.state.quiz[this.state.activeQuestion].answers}
                            question={this.state.quiz[this.state.activeQuestion].question}
                            onAnswerClick={this.onAnswerClickHandler}
                            quizLength={this.state.quiz.length}
                            answerNumber={this.state.activeQuestion + 1}
                            state={this.state.answerState}
                         />
                  }
                </div>
            </div>
        )
    }
}

export default Quiz